main {
  padding-top: 32px;
}

.package {
  margin: 16px 0;
  width: 80%;
  border: 4px #0e4f1f solid;
  border-left: none;
}
.package:first-child {
  background: #bfffd0;
}
.package:nth-child(2) {
  background: #e9ffef;
}
.package:nth-child(3) {
  background: #0e4f1f;
}
.package a {
  padding: 32px;
  text-decoration: none;
  display: block;
  color: inherit;
}
.package:hover,
.package:active {
  border: coral 5px solid;
}

.main-section__title {
  color: black;
}
.white {
  color: white;
}

.package h2 {
  color: #858585;
}
.package p {
  padding: 16px 8px;
  color: #093b16;
  border: #093b16 solid 1.5px;
  background: white;
}
